article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}

/*****************************************************/
/*************Body and General Styles*****************/
/*****************************************************/
@font-face{
    font-family: "ABZ-regular";
    src:
        url(${___CSS_LOADER_URL_REPLACEMENT_0___}) format("woff2"), 
        url(${___CSS_LOADER_URL_REPLACEMENT_1___}) format("woff");
    font-weight: normal;
    font-style: normal;
}

body{
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    background-size: 100% 100%;
    font-family: sans-serif, system-ui;
}

ul > li{
    list-style: none;
}

/*****************************************************/
/*************Nav Bar Styles**************************/
/*****************************************************/
nav{
    height: 10vh;
    background-color: rgb(255, 255, 255);
    padding: 16px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: start;
    gap: 24px;
}

nav > button{
    position: relative;
    width: 100px;
    height: 50px;
    border: none;
    border-radius: 4px;
    background-color: rgb(65, 29, 0);
    color: white;
    font-size: 2rem;
}

nav > button:hover{
    box-shadow: 2px 2px 1px 1px gray;
}

nav > button.currently-active::after{
    content: "";
    position: absolute;
    bottom: -50px;
    right: 0px;
    height: 50px;
    width: 100px; 
    background: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    background-size: 100% 100%;
}

/*****************************************************/
/**************Main div styles************************/
/*****************************************************/
div#page-content{
    position: relative;
    min-height: calc(80vh); /*Footer and nav bar are 10 and 10vh.*/
}

div#page-content > a.background-cred{
    position: absolute;
    top: -22px;
    right: 8px;
    font-style: italic;
    color: white;
}

div#page-content > a.background-cred:hover{
    font-size: 1.2rem;
}

/*****************************************************/
/**************Home div styles************************/
/*****************************************************/
div.home-div,
div.about-div{
    min-height: 60vh;
    width: clamp(400px, 75vw, 1000px);
    margin: 24px auto;
    padding: 24px;
    background: rgba(65, 29, 0, 0.9);
    border-radius: 24px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    gap: 12px;
}

div.home-div > *,
div.about-div > *{
    background-color: white;
    border-radius: 8px;
    padding: 16px;
}

div.home-div h1,
div.home-div h2,
div.about-div h1,
div.about-div h2{
    font-family: ABZ-regular, sans-serif, system-ui;
    font-weight: bolder;
}

div.home-div h1,
div.about-div h1{
    font-size: 2rem;
}

div.home-div h2,
div.about-div h2{
    font-size: 1.5rem;
    margin-bottom: 16px;
    text-decoration: underline;
    text-decoration-thickness: 0.4rem;
}

div.home-div li,
div.home-div p,
div.about-div li{
    font-size: 1.2rem;
}

div.home-div > p{
    font-style: italic;
    text-align: justify;
}

/*****************************************************/
/**************Menu Div Styles************************/
/*****************************************************/
div.menu-div{
    min-height: 60vh;
    width: clamp(400px, 75vw, 1000px);
    margin: 24px auto;
    padding: 24px;
    background: rgba(65, 29, 0, 0.9);
    border-radius: 24px;
}

div.menu-div > div.menu-item{
    margin-bottom: 16px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    gap: 24px;
}

div.menu-div > div.menu-item > div.menu-image{
    width: 40%;
    position: relative;
}

div.menu-div > div.menu-item > div.menu-image > img{
    width: 100%;
    height: 300px;
}

div.menu-div > div.menu-item > div.menu-image > a{
    position: absolute;
    top: 8px;
    right: 8px;
    color: white;
    font-style: italic;
}

div.menu-div > div.menu-item > div.menu-image > a:hover{
    font-size: 1.2rem;
}

div.menu-div > div.menu-item > div.menu-desc{
    width: 60%;
    box-sizing: border-box;
    background-color: white;
    border-radius: 8px;
    padding: 16px;
}

div.menu-div > div.menu-item > div.menu-desc > h1{
    margin-bottom: 16px;
    font-size: 2rem;
    font-weight: bolder;
    font-family: ABZ-regular, sans-serif, system-ui;
}

div.menu-div > div.menu-item > div.menu-desc > p{
    font-size: 1.2rem;
    font-style: italic;
    text-align: justify;
}
/*****************************************************/
/**************Footer Styles**************************/
/*****************************************************/
footer{
    position: sticky;
    bottom: 0px;
    width: 100%;
    height: 10vh;
    box-sizing: border-box;
    padding: 0px 16px;
    background-color: rgb(255, 255, 255);
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

footer a{
    font-style: italic;
}

footer a:hover{
    font-size: 1.2rem;
}

footer a:link,
footer a:visited{
    color: black;
}

/*****************************************************/
/*********Test Button Styles**************************/
/*****************************************************/
button#test-button{
    width: 100px;
    height: 100px;
    display: block;
    margin: 0 auto;
    border: 1px solid black;
    border-radius: 8px;
    background-color: rgb(241, 241, 241);
    color: red;
}

button#test-button:hover{
    box-shadow: 1px 1px 1px 1px rgb(112, 112, 112);
}

button#test-button:active{
    box-shadow: inset 0px 0px 1px 1px rgb(112, 112, 112);
